import { TRANSACTION_LIST_SUCCESS } from '../constants/actionTypes'

const initialState = {
  transactionList: [],
}

const TransactionReducer = (state = initialState, action) => {
  switch (action.type) {
    case TRANSACTION_LIST_SUCCESS:
      return {
        ...state,
        transactionList: action.data,
      }
    default:
      return state
  }
}

export default TransactionReducer
