import { Container } from 'react-bootstrap'
import { Routes, Route } from 'react-router-dom'
import jwt_decode from 'jwt-decode'
// Layout
import Layout from './layout/Layout'

// pages
import Home from './pages/Home'
import About from './pages/About'
import Register from './pages/admin/Register'
import Login from './pages/admin/Login'
import NotFound from './pages/NotFound'

import Dashboard from './pages/admin/Dashboard'
import { SET_CURRENT_USER as SET_CURRENT_ADMIN } from './constants/admin/actionTypes'
import { SET_CURRENT_USER } from './constants/actionTypes'
import store from './store'

let token = localStorage.getItem('adminAuthToken')
if (token) {
  let admin = jwt_decode(token)

  store.dispatch({ type: SET_CURRENT_ADMIN, data: admin })
}

token = localStorage.getItem('authToken')
if (token) {
  let user = jwt_decode(token)

  store.dispatch({ type: SET_CURRENT_USER, data: user })
}

const App = () => {
  return (
    <Layout>
      <Container>
        <Routes>
          <Route path="/admin/dashboard" element={<Dashboard />} exact />
          <Route path="/admin/register" element={<Register />} exact />
          <Route path="/admin/login" element={<Login />} exact />

          <Route path="/" element={<Home />} exact />
          <Route path="/about" element={<About />} exact />
          <Route element={<NotFound />} />
        </Routes>
      </Container>
    </Layout>
  )
}

export default App
